import * as React from "react";
import "./chat.css"

import { ChatServicePromiseClient } from "../../generated/chat_grpc_web_pb";
import { VoidMessage, ChatMessageRequest } from "../../generated/chat_pb";

export interface ChatProps {
    client: ChatServicePromiseClient;
    className: string;
}

export default (props: ChatProps) => {
    const inputRef = React.useRef(null);
    const logRef = React.useRef(null);

    const name = Math.random().toString(36).substring(7);

    React.useEffect(() => {
        const request = new VoidMessage();
        const handler = props.client.connect(request).on("data", value => {
            updateChatLog(value.getSender(), value.getMessage());
        }).on("error", error => {
            console.log(error);
        }).on("status", status => {
            console.log(status);
        }).on("end", () => {
            console.log("end");
        });

        return () => { handler.cancel() }
    }, [])

    const updateChatLog = (sender: string, value: string) => {
        logRef.current.value += sender + ": " + value + "\n";
        logRef.current.scrollTop = logRef.current.scrollHeight;
    }

    const handleSend = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const input = inputRef.current.value;

        if (input !== "") {
            const request = new ChatMessageRequest();
            request.setSender(name);
            request.setMessage(input);
            props.client.send(request);
            inputRef.current.value = "";
        }
    };

    return (
        <div className={props.className}>
            <textarea className="chatLog" disabled ref={logRef}></textarea>
            <form className="chatBar" onSubmit={handleSend}>
                <input className="chatInput" type="text" ref={inputRef} />
                <input className="chatButton btn btn-primary" type="submit" value="Send" />
            </form>
        </div>
    )
}
