import "../assets/base.css";
import "./main.css";

import * as React from "react";
import * as ReactDOM from "react-dom";

import { EchoServicePromiseClient } from "../../generated/echo_grpc_web_pb";
import { ChatServicePromiseClient } from "../../generated/chat_grpc_web_pb";

import Echo from "./echo";
import Chat from "./chat";

declare var SERVICE_HOST: string;

var element;
const component = window.location.hash
if (component == "#chat") {
    const chatClient = new ChatServicePromiseClient(SERVICE_HOST);
    element = <Chat client={chatClient} className="chat"/>
} else {
    const echoClient = new EchoServicePromiseClient(SERVICE_HOST);
    element = <Echo client={echoClient} className="echo"/>
}

ReactDOM.render(
    element,
    document.getElementById("canvas")
);
